.reset();

.container {
  .wrapper();
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #8BBEAD;
  text-decoration: none;
}

button{
  cursor: pointer;
}

abbr {
  text-decoration: none;
}