body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: #8BBEAD;
  text-decoration: none;
}

button {
  cursor: pointer;
}

abbr {
  text-decoration: none;
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.main-header {
  color: #8BBEAD;
  background-color: #0E0E0E;
}

.discounts--item {
  display: none;
}

.discounts--item__active {
  display: block;
}

.discounts--check {
  font-size: 0;
  border: none;
  padding: 0;
  width: 10px;
  height: 10px;
  background-color: transparent;
  border: 1px solid #8BBEAD;
  border-radius: 50%;
}

.services--list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: center;
}

.services--item {
  background-color: rgba(255, 0, 0, 0.5);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(50% - 30px);
  margin-bottom: 20px;
}

.service--list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  justify-content: space-around;
}

.service--item {
  background-color: rgba(255, 255, 0, 0.5);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(25% - 30px);
}

.service--header {
  text-align: center;
}

.faq--container {
  display: flex;
  justify-content: center;
}

.faq--header {
  display: inline-block;
  margin-right: 4px;
}

.faq--text {
  display: inline-block;
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .service--item {
    width: calc(41.66666666666667% - 30px);
  }
}

@media screen and (max-width: 560px) {
  .services--item {
    width: calc(83.33333333333334% - 30px);
  }

  .service--item {
    width: calc(100% - 30px);
  }
}